.NodePicker__container {
    background: #eeeeee;
    padding: 8px;
    border-radius: 4px;
    -webkit-box-shadow: 0 0 0 1px rgba(255,255,255,0.8), 0 2px 6px rgba(0,0,0,0.4);
            box-shadow: 0 0 0 1px rgba(255,255,255,0.8), 0 2px 6px rgba(0,0,0,0.4);
}

.Popover-tip polygon {
    fill: #eeeeee;
}

.NodePicker ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    list-style-position: inside;
    font-family: monospace;
    font-size: 12px;
}

.NodePicker__pick {
    padding: 4px;
    margin-bottom: 4px;
}

.NodePicker__pick.active {
    background: #1CAAD9;
    color: #FFF;
}

.NodePicker > ul {
    max-height: 200px;
    max-width: 300px;
    overflow: auto;
    padding: 4px;
    background: #ffffff;
    border: 1px solid #dddddd;
}

.NodePicker ul ul {
    padding: 0 0 0 8px;
}

.NodePicker li {
    margin: 0;
    padding: 0;
    cursor: pointer;
}

.NodePicker__node {
    display: block;
    letter-spacing: 0px;
    white-space: nowrap;
    border-radius: 2px;
    padding: 2px;
}

.NodePicker__node svg {
    fill: #888888;
}

.NodePicker__node.selected {
    background: #1CAAD9;
    color: #FFF;
}
.NodePicker__node.selected svg {
    fill: #ffffff;
}



