.option {
    background: #ffffff;
    border: 1px solid #ddd;
    border-radius: 3px;
    margin: 10px 0;
    font-size: 13px;
}

.option__header {
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #eee;
    text-transform: uppercase;
    color: #555;
}

.option__header b {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    padding: 8px;
    font-weight: normal;
}

.option__content {
    padding: 8px;
}

.option__content > div {
    padding: 4px 0;
}

.option.--collapsed .option__content {
    display: none;
}

.option.--collapsed .option__collapse svg {
    -webkit-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.option__row {
    display: -ms-flexbox;
    display: flex;
}

.option__row label {
    -ms-flex: 0 0 40%;
        flex: 0 0 40%;
    color: #555;
    min-height: 28px;
    line-height: 28px;
}



