html,
body,
#root {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    font-size: 14px;
    height: 100%;
    width: 100%;
}

.input.--text {
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 2px 4px;
    font-size: 14px;
}

.input.--text:focus {
    border: 1px solid #1caad9;
    outline: 0;
}

.select {
    font-size: 14px;
}

.button {
    border: 0;
    background: #e7e7e7;
    color: #555555;
    border-radius: 4px;
    padding: 4px 8px;
    font-size: 13px;
    font-family: 'Titillium Web', sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    cursor: pointer;
    -webkit-transition: background 0.2s;
    -o-transition: background 0.2s;
    transition: background 0.2s;
}

.button.--medium {
    padding: 6px 12px;
}

.button:hover {
    background: #cecece;
    color: #333333;
}

.button.--primary {
    background: #1caad9;
    color: #ffffff;
}

.button.--primary:hover {
    background: #1687ac;
    color: #ffffff;
}

.button:disabled {
    pointer-events: none;
    opacity: 0.5;
}

.button.--icon {
    background: transparent;
    color: #333;
}

.button.--icon:hover {
    color: #1caad9;
}

.button.--select svg {
    height: 13px;
    width: 13px;
    vertical-align: middle;
    margin: -4px 4px 0 4px;
}

select,
input[type='text'] {
    font-size: 16px;
}



