.ReactModal__Content {
    opacity: 0;
}

.ReactModal__Content--after-open {
    opacity: 1;
    -webkit-transition: opacity 0.2s;
    -o-transition: opacity 0.2s;
    transition: opacity 0.2s;
}

.ReactModal__Content--before-close {
    opacity: 0;
}

.PopupAboutModelOverlay {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: rgba(0, 0, 0, 0.0);
    -webkit-transition: background-color 0.1s;
    -o-transition: background-color 0.1s;
    transition: background-color 0.1s;
}

.ReactModal__Overlay--after-open {
    background-color: rgba(0, 0, 0, 0.3);
}

.PopupAboutModal {
    background: #ffffff;
    border-radius: 6px;
    margin: 100px auto;
    max-width: 600px;
    -webkit-box-shadow: 0 11px 20px rgba(97, 108, 121, 0.17);
            box-shadow: 0 11px 20px rgba(97, 108, 121, 0.17);
}

.PopupAbout {
    font-family: 'Open Sans', sans-serif;
}

.PopupAbout__header {
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #e7e7e7;
}

.PopupAbout__header h2 {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    font-size: 21px;
    font-weight: 200;
    color: #555555;
    padding: 20px;
    margin: 0;
}

.PopupAbout__header button {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: 0;
    background: transparent;
    margin: 0;
    padding: 20px;
    cursor: pointer;
}

.PopupAbout__header svg {
    width: 20px;
    height: 20px;
    fill: #cccccc;
}

.PopupAbout__header button:hover svg {
    fill: #1caad9;
}

.PopupAbout__content {
    padding: 20px;
    color: #555555;
    line-height: 24px;
}

.PopupAbout__content a {
    color: #1caad9;
    text-decoration: none;
}



